<template>
	<view>
		<u-sticky>
			<u-navbar title="我的" bgColor="transparent" leftIcon=" " :fixed="false" :placeholder="true" :titleStyle="{
			'fontsize':'10',
			color:'#000000'
		}">
			</u-navbar>
		</u-sticky>
		<view class="vie1">
			<u-avatar size="63"
				:src="user.image?user.image:'https://zhsq.0898yzzx.com/static/xcx-img/touxiang-default.png'"></u-avatar>
			<view class="my" @click="nav(user?'../mine/edit':'./login')">
				<block v-if="!user.nickname">
					<text>请登录</text>
				</block>
				<block v-else>
					<text>{{user.nickname}}</text>
					<text>{{user.phone}}</text>
				</block>
			</view>
		</view>
		<view class="vie2">
			<lable>消费 <lable class="lab1">{{user.saleprice}}</lable>
			</lable>
			<lable>订单数 <lable class="lab1">{{user.all_order}}</lable>
				</h1>
			</lable>
			<lable>待处理 <lable class="lab1">{{user.pending_order}}</lable>
			</lable>
		</view>
		<view class="bg-color">
			<view class="wode_card">
				<image src="../static/card_bg.png" mode=""></image>
				<view class="box">
					<view style="display: flex;justify-content: space-between;align-items: center;">
						<text class="title_icon">
							<image src="../static/title_icon.png" mode="widthFix"></image>我的会员
						</text>
						<view style="display: flex;font-size: 20rpx;color: #fff;">
							<text @click="show = true">会员权益</text>
							<u-icon name="arrow-right" color="white" size="10"></u-icon>
						</view>
					</view>
					<u-popup :show="show" @close="close" @open="open" mode="center" round="10" closeable="true"
						safeAreaInsetTop="true" customStyle="width:80%">
						<view class="popup">
							<text>本平台会员充值，充值100送30元赠送金、200送80元赠送金、300送120元赠送金。
								会员每年生日可享受一次30元内免单机会！
								一元可得1积分，积分到达3888，可从普通会员升级为黄金会员，并增加80元赠送金。
								积分到达6888，可从黄金会员升为白金会员，并增加180元赠送金。
								充值金和赠送金无日期限制。</text>
							<text>赠送金说明</text>
							<text>如何获取赠送金</text>
							<text>在圈圈平台余额账户或银行电子账户充值后可享受返赠送金活动。</text>
							下单抵现当钱花
							<text>赠送金是在圈圈平台消费时按照1：1抵扣订单金额使用。</text>
							赠送金无法提现
							<text>充值本金可提现，赠送金不可提现，充值本金一旦提现，赠送金将被清零。</text>
						</view>
					</u-popup>
					<view class="card">
						<text class="name">白银会员</text>
						<view class="">
							<text style="margin: 8rpx 0;display: block;">50/100</text>
							<u-line-progress :showText="showText" :inactiveColor="inactiveColor"
								:activeColor="activeColor" :percentage="percentage" height="6" />
							<text style="font-size: 20rpx;">成长值还差1进阶SVP1,享8折</text>
						</view>
					</view>
				</view>
			</view>
			<view class="vie4">
				<view class="title_icon">
					<image src="../static/title_icon_1.png" mode="widthFix"></image>我的权益
				</view>
				<view class="my_mb">
					<view class="mb" @click="coupon">
						<image src="../static/model.png" mode="widthFix"></image>
						<view class="title1">
							<text style="">优惠卷</text>
						</view>
						<text><text style="color: #FD664F;">{{user.usercunpun}}</text>张未使用</text>
					</view>
					<view class="mb" @click="month">
						<image src="../static/model1.png" mode="widthFix"></image>
						<view class="title1">
							<text style="">月卡</text>
						</view>
						<text><text style="color: #FD664F;">{{user.usercard}}</text>张未使用</text>
					</view>
					<view class="mb" @click="wallet">
						<image src="../static/model2.png" mode="widthFix"></image>
						<view class="title1">
							<text style="">钱包</text>
						</view>
						<text><text style="color: #FD664F;">0</text>余额(元)</text>
					</view>
					<view class="mb" @click="makemoney">
						<image src="../static/model3.png" mode="widthFix"></image>
						<view class="title1">
							<text style="">赚零钱</text>
						</view>
						<text><text style="color: #FD664F;">0</text>余额(元)</text>
					</view>
				</view>
			</view>
			<view class="vie4">
				<view class="title_icon">
					<image src="../static/title_icon.png" mode="widthFix"></image>我的功能
				</view>
				<view class="my_mb">
					<view class="mb" @click="wodedizhi">
						<image src="../static/wode_1.png" mode="widthFix"></image>
						<text style="color: black;">我的地址</text>
					</view>
					<view class="mb" @click="personal">
						<image src="../static/wode_2.png" mode="widthFix"></image>
						<text style="color: black;">我的资料</text>
					</view>
					<view class="mb" @click="dingdan">
						<image src="../static/wode_3.png" mode="widthFix"></image>
						<text style="color: black;">我的订单</text>
					</view>
					<view class="mb" @click="customer_service">
						<image src="../static/wode_4.png" mode="widthFix"></image>
						<text style="color: black;">客服中心</text>
					</view>
				</view>
			</view>
		</view>
		<t-s :current="current"></t-s>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				show: false,
				current: 2,
				xiaofei: 0,
				daichuli: 0,
				user: '',
				type: uni.getStorageSync('user').user_type ? uni.getStorageSync('user').user_type : 1,
				showText: false,
				inactiveColor: '#fff',
				activeColor: '#9DE4FE',
				percentage: 20
			}
		},
		onLoad() {
			var res = global.isLogin();
			if (!res) {
				uni.navigateTo({
					url: "/pages/login"
				});
			}
		},
		onShow() {
			uni.showLoading({
				title: '加载中'
			})
			let id = 0;
			// console.log(uni.getStorageSync('user').user_id)
			switch (uni.getStorageSync('user').user_type) {
				case 1:
					id = uni.getStorageSync('user').user_id
					break;
				case 2:
					id = uni.getStorageSync('user').staff_id
					break;
				case 3:
					id = uni.getStorageSync('user').distributor_id
					break;
				default:
					break;
			}
			uni.$u.http.post('api/users/getuserallinfo', {
				user_id: uni.getStorageSync('user'),
				campus_id: uni.getStorageSync('campus_id')
			}).then(res => {
				// console.log('获取用户信息', res.data)
				this.user = res.data.data
				uni.hideLoading()
			}).catch(err => {
				// console.log('shibai', err)
				uni.hideLoading()
			})
		},
		methods: {
			open() {
				// console.log('open');
			},
			close() {
				this.show = false
				// console.log('close');
			},
			dingdan() {
				uni.switchTab({
					url: './dingdan'
				})
			},
			customer_service() {
				uni.navigateTo({
					url: './customer_service/index'
				})
			},
			wodedizhi() {
				uni.navigateTo({
					url: './address/wodedizhi'
				})
			},
			wallet() {
				uni.navigateTo({
					url: './wallet/index'
				})
			},
			makemoney() {
				uni.navigateTo({
					url: './makemoney/index'
				})
			},
			personal() {
				uni.navigateTo({
					url: './personal/index'
				})
			},
			nav(url) {
				if (!this.user && url != './login') {
					uni.showToast({
						title: '请先登录,2秒后自动跳转登录页面',
						icon: 'none'
					})
					setTimeout(function() {
						uni.navigateTo({
							url: './login'
						})
					}, 2000)

					return
				}
				uni.navigateTo({
					url: url
				})
			},
			coupon() {
				uni.navigateTo({
					url: 'coupon/index'
				})
			},
			month() {
				uni.navigateTo({
					url: './card/month'
				})
			}
		}
	}
</script>

<style>
	page {
		background: linear-gradient(to right, #fedb74, #ffbf80);
	}

	.u-navbar {
		background: linear-gradient(to right, #fedb74, #ffbf80);
	}

	.popup {
		padding: 20rpx;
		overflow-y: scroll;
		max-height: 90vh;
		font-size: 13px;
	}

	.bg-color {
		border-top-left-radius: 60rpx;
		border-top-right-radius: 60rpx;
		background: linear-gradient(to bottom, #fefefd, #eeebe1);
		padding: 34rpx;
		margin-top: 20rpx;
	}

	.my_mb {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.vie4>view {
		font-size: 26rpx;
	}

	.mb>image {
		width: 72rpx;
		margin-bottom: 20rpx;
	}

	.box {
		position: absolute;
		top: 30rpx;
		left: 30rpx;
		width: 92%;
	}

	.box .title_icon {
		width: 128rpx;
		height: 46rpx;
		line-height: 46rpx;
		background: #FFFFFF;
		border-radius: 24rpx;
		font-size: 20rpx;
		font-weight: 500;
		color: #FD684F;
		text-align: center;
	}

	.card {
		border-radius: 20rpx;
		color: white;
		width: 75%;
	}

	.card .name {
		font-size: 66rpx;
		font-family: YouSheBiaoTiHei;
		font-weight: bold;
		color: #FFFFFF;
		font-style: italic;
	}

	.wode_card {
		position: relative;
		box-shadow: 0px 0px 10px 0px rgba(51, 51, 51, 0.17);
		border-radius: 30rpx;
		height: 286rpx;
	}

	.wode_card image {
		width: 100%;
		height: 100%;
	}

	.my_mb .mb {
		display: flex;
		flex-flow: column;
		align-items: center;
		font-size: 26rpx;
		color: #808080;
	}

	.title1 {
		font-size: 22rpx;
		color: black;
		margin-bottom: 14rpx;
	}

	.title1 image {
		width: 50rpx;
	}

	.vie4 .title_icon {
		display: flex;
		align-items: center;
		margin-right: 6rpx;
		margin-bottom: 22rpx;
	}

	.vie4 .title_icon image {
		width: 24rpx;
		margin-right: 6rpx;
	}

	.vie1 {
		/* height: 300rpx; */
		background: linear-gradient(to right, #fedb74, #ffbf80);
		display: flex;
		align-items: center;
		padding: 20rpx 5% 20rpx 5%;
	}

	.my {
		display: flex;
		flex-flow: column;
	}

	.my text {
		font-size: 40rpx;
		color: #101010;
		margin-left: 20rpx;
	}

	.vie1>image {
		width: 100rpx;
		height: 100rpx;
		border-radius: 50%;
	}

	.vie2 {
		display: flex;
		justify-content: space-around;
		align-items: center;
		width: 90%;
		height: 140rpx;
		margin: auto;
		background-color: rgba(255, 255, 255, 0.65);
		border-radius: 20rpx;
		font-size: 28rpx;
		color: dimgrey;
	}

	page {
		background-color: #eeeeef;
	}

	.lab1 {
		font-size: 38rpx;
		font-weight: bold;
		color: black;
		margin-left: 8rpx;
	}

	.vie3 {
		display: flex;
		align-items: center;
		width: 82%;
		height: 120rpx;
		margin: auto;
		background-color: #FFFFFF;
		border-radius: 15rpx;
		margin-top: 90rpx;
		position: relative;
		box-shadow: 0px 0px 1px 1px lightgrey;
		padding: 0 30rpx;
	}

	.vie3 lable,
	.vie4 lable {
		margin-left: 30rpx;
	}

	.vie3 image {
		width: 50rpx;
	}

	.vie4 {
		display: flex;
		flex-flow: column;
		background-color: #FFFFFF;
		border-radius: 30rpx;
		margin: 34rpx auto;
		box-shadow: 0px 0px 10px 0px rgba(51, 51, 51, 0.17);
		padding: 30rpx;
	}
</style>
